import React from "react";
import PropTypes from "prop-types";
import Space from "./../layout/Space";
import CheckboxValue from './CheckboxValue';


export default class Checkbox extends React.PureComponent {
  myRef = null;

  constructor(props) {
    // Call super class
    super(props);

    this.state={
    currentID: -1,
  }
}


  handleChange = (e) => {
    
    this.props.onChange(e.target.checked);
   // CheckboxValue(this.props.id);
    this.state.currentID=this.props.id;
    window.alert(this.state.currentID+ " " + this.props.label);
  };

  render() {
    const { id, checked, label } = this.props;

    return (

        <div>

        

        <label>
          {id}
        <input
          
          ref={node => (this.myRef = node)}
          type="checkbox"
          checked={checked}
          onChange={this.handleChange }
        />

        
        <Space n={3} />
          {label}
        </label>

        <div>
          {CheckboxValue(this.state.currentID)}
        </div>
      </div> 

      
    );
  }
}

Checkbox.propTypes = {
  id: PropTypes.number.isRequired,
  label: PropTypes.string.isRequired,
  checked: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired
};