import React from 'react'
import ReactDOM from 'react-dom'
import mapboxgl from 'mapbox-gl'

mapboxgl.accessToken = 'pk.eyJ1IjoibGlnaHRidXJuIiwiYSI6ImNpeXViOGptcDAwMmYzMmxmZml6am0xZG0ifQ.FiaHv8Pwcxr_LyuxMtry3w';

class MapboxContainer extends React.Component {

   map;

    state = {
        activate: null,
      };
    
      clickActivate = () => {
        const activate = '/item41/a';
    
        this.setState({ activate });
      };
    
    
      constructor(props) {
        super(props);
        this.state = {
          lng: 5,
          lat: 34,
          zoom: 1.5
        };
      }
      
      
    
      componentDidMount() {
        const { lng, lat, zoom } = this.state;
    
         this.map = new mapboxgl.Map({
          container: this.mapContainer,
          style: 'mapbox://styles/mapbox/satellite-streets-v9',
          center: [lng, lat],
          zoom
        });
        
    
        this.map.on('move', () => {
          const { lng, lat } = this.map.getCenter();
    
          this.setState({
            lng: lng.toFixed(4),
            lat: lat.toFixed(4),
            zoom: this.map.getZoom().toFixed(2)
          });
        });
      }
    
      render() {

        
        const { lng, lat, zoom } = this.state;
    
        return (
          <div>
            <div className="mapTextContainer">
              <div>{`Longitude: ${lng} Latitude: ${lat} Zoom: ${zoom}`}</div>
            </div>
            <div ref={el => this.mapContainer = el} className="mapContainer" />
          </div>
          
          
        );
      }
    }
    
    export default MapboxContainer;
    //ReactDOM.render(<Application />, document.getElementById('app'));
    //ReactDOM.render(<Application />, document.getElementById('root'));