import React, { Component } from 'react';
import {useDispatch} from 'react-redux';
import {set_met_10m_u_component_of_wind} from './../actions';

function SetStateValueForSelectedCheckbox(id){

    const dispatch = useDispatch();

    if(id.idValue===1){
        window.alert("ESTA NOCHE DOY SERRUCHO");
        dispatch(set_met_10m_u_component_of_wind(id.idValue));
        console.log("ID VALUE IS :" + id.idValue);
   //    id.idValue_;
    }

   
   

    return(
        <p></p>
    );
}

function CheckboxValue(id){

   // window.alert("SACA LA MAIZENA 3 " + id)
   // const dispatch = useDispatch();

 //   dispatch(set_met_10m_u_component_of_wind(id));
    console.log("Arriving Value is :" + id);
    return(
       
        <div>     
            <SetStateValueForSelectedCheckbox
            idValue ={id}
            />
        </div>
    );

}

export default CheckboxValue;